* {
  box-sizing: border-box;
  min-width: 0;
  min-height: 0; }

body {
  background: $c-main;
  @include rmax(sm) {
    font-size: 14px; } }

img {
  max-width: 100%; }

.page {
  position: relative; }

.container {
  max-width: 1180px;
  margin: auto;
  padding: 0 20px; }

.section {
  padding: 120px 0;
  &__footer {
    margin-top: 80px;
    text-align: center; }
  &--full {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center; }
  &--dark {
    background: $c-main url("../../img/bg-section.png"); } }

.btn-wrap {
  margin-top: 120px;
  &--center {
    text-align: center; } }

.btn {
  cursor: pointer;
  display: inline-flex;
  align-items:center {}
  line-height: 1;
  font-size: 14px;
  text-transform: uppercase;
  white-space: nowrap;
  border: none;
  text-decoration: none;
  padding: 1.1em 1.8em;
  color: #fff;
  border-radius: .2em;
  transition: all .2s;
  .ico {
    font-size: 9px;
    margin: 1px 0  0 12px;
    line-height: 0; }
  &--fill {
    background-color: #E41139;
    &:hover {
      color: #fff;
      background-color: #D3002F;
      box-shadow: 0 2px 12px rgba(#000, .5); }
    &:active {
      background-color: #E34C6E; } }

  &--outline {
    background-color: transparent;
    color: $c-brand;
    border: 1px solid $c-brand;
    &:hover {
      color: $c-brand;
      background-color: rgba(#000, .2); }
    &:active {
      background-color: rgba(#000, .4); } } }

.inp-styl {
  & + & {
    margin-top: 1em; }
  input {
    position: absolute;
    z-index: -1;
    opacity: 0; }
  label {
    cursor: pointer;
    position: relative;
    display: inline-flex;
    align-items: center;
    &:before {
      display: inline-block;
      content: "";
      width: 1em;
      height: 1em;
      border: 2px solid $c-sec;
      margin-right: .75em;
      flex: 0 0 1em; } }

  input[type="radio"] {
    & + label:before {
      border-radius: 50%; }

    &:checked + label:before {
      border-color: $c-brand;
      background-image: radial-gradient($c-brand 40%,  transparent 40%);
      animation: scale-input .3s cubic-bezier(0.39, 0.58, 0.57, 1); } }

  input[type="checkbox"] {
    & + label:before {
      border-radius: 2px; }

    &:checked + label:before {
      border-color: $c-brand;
      background: $c-brand 50% no-repeat url('data:image/svg+xml;base64,');
      background-size: 80% auto;
      animation: scale-input .3s cubic-bezier(0.39, 0.58, 0.57, 1); } } }

@keyframes scale-input {
  50% {
    transform: scale(1.2); }
  100% {
    transform: scale(1); } }

.title-section {
  font-weight: bold;
  text-align: center;
  font-size: 20px;
  margin-bottom: 2em;
  @include r(lg) {
    margin-bottom: 120px;
    font-size: 26px; } }

.subtitle-section {
  display: block;
  text-align: center;
  font-size: .8em;
  color: $c-sec;
  font-weight: normal;
  margin: 1em auto 0;
  max-width: 460px; }

.ul-list {
  margin: 0;
  padding: 0;
  list-style: none;
  &__item {
    padding-left: 30px;
    margin-bottom: 1.5em;
    position: relative;
    @include r(lg) {
      font-size: 20px; }
    &:before {
      content: "";
      display: block;
      position: absolute;
      top: .5em;
      left: 0;
      margin-right: 20px;
      width: .5em;
      height: .5em;
      border-radius: 50%;
      background-color: #fff;
      vertical-align: middle; } } }

.soc {
  margin-bottom: 40px;
  &__link {
    display: inline-block;
    width: 56px;
    height: 56px;
    line-height: 58px;
    font-size: 24px;
    text-align: center;
    border: 1px solid;
    border-radius: 50%;
    &:not(:last-child) {
      margin-right: 20px; }
    &:hover {
      color: #fff;
      background-color: rgba(#fff, .3); }
    &:active {
      background-color: rgba(#fff, .5); } } }

.range {
  width: 100%;
  margin: 0;
  background-color: transparent;
  -webkit-appearance: none;
  &:focus {
    outline: none; } }

.range-wrap {
  position: relative; }

.range-sel {
  pointer-events: none;
  position: absolute;
  top: 50%;
  left: 0;
  width: 0%;
  height: 4px;
  background: $c-brand;
  border-radius: 2px; }

.bubble {
  position: absolute;
  background-color: #fff;
  padding: 10px;
  top: 30px;
  font-size: 14px;
  border-radius: 10px;
  color: #000;
  z-index: 100;
  transition: all .2s;
  user-select: none;
  white-space: nowrap; }

input[type=range]::-webkit-slider-runnable-track {
  -webkit-appearance: none;
  background: #fff;
  width: 100%;
  height: 6px;
  cursor: pointer;
  border: 0.2px solid #010101; }

input[type=range]::-webkit-slider-thumb {
  -webkit-appearance: none;
  height: 24px;
  width: 24px;
  border-radius: 50%;
  background: $c-brand;
  cursor: pointer;
  margin-top: -12px;
  box-shadow: 1px 1px 3px #000000; }

